    svg: `<svg focusable="false" preserveAspectRatio="xMidYMid meet" style="will-change: transform;" xmlns="http://www.w3.org/2000/svg" width="32" height="32" viewBox="0 0 32 32" aria-hidden="true"><path d="M12 11L15 11 15 23 17 23 17 11 20 11 20 9 12 9 12 11z"></path></svg>`
